.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.black-nav{
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
  font-weight: 600;
  font-size: 20px;
  box-sizing: border-box;
}

.nav-button{
  background: Black;
  color:White;
  width: 90px;
  height: 30px;
  border:white;
  border-style: solid;
  border-radius: 15%;
  border-width: 2px;
  float:left;
  margin: 5px;
}